#questions{background-color:#f6f9fc;align-self:stretch;padding-top:20px;text-align:center;padding-top:35px;padding-bottom:70px}@media(max-width: 800px){#questions{padding-top:0;padding-bottom:35px}}#questions>:nth-child(1){width:calc(44em + 30vw);display:flex;margin:auto;flex-direction:column;flex-shrink:1}@media(max-width: 1024px){#questions>:nth-child(1){width:calc((.03em + 4.1vw)*23)}}#questions>:nth-child(1) #questions__title{align-self:center;font-size:38px;font-weight:600;margin:30px 0}#questions>:nth-child(1)>:nth-child(2){display:flex;flex-direction:column;text-align:start;gap:5px}#questions>:nth-child(1)>:nth-child(2) input{position:absolute;opacity:0;z-index:-1}#questions>:nth-child(1)>:nth-child(2) .questions__item{display:flex;background:rgba(255,255,255,.95);box-shadow:0px 4px 44px rgba(84,104,123,.05);backdrop-filter:blur(12px);border-radius:22px;flex-direction:column;border-radius:12px;width:100%;transition:all .3s;cursor:pointer;box-shadow:0 0 10px rgba(1,194,253,.1)}#questions>:nth-child(1)>:nth-child(2) .questions__item:hover{background:rgba(0,0,0,0);transition:none;box-shadow:0 0 25px rgba(1,194,253,.3)}#questions>:nth-child(1)>:nth-child(2) .questions__item:hover>:nth-child(1):after{color:rgba(1,194,253,.3)}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(1){font-size:20px;font-weight:600;width:100%;display:flex;justify-content:space-between;transition:all .3s;cursor:pointer;padding:25px 35px}@media(max-width: 425px){#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(1){font-size:16px;padding:25px 20px}}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(1)::after{content:"❯";transform:rotate(90deg);width:1em;height:1em;text-align:center;transition:all .3s;margin-right:.5vw}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(2){color:#000;line-height:19px;overflow:auto;padding-right:15px;font-weight:300;max-height:0vh;text-align:start;font-size:16px;font-weight:400;overflow:hidden;padding:0 7% 0 35px;zoom:.01}@media(max-width: 425px){#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(2){font-size:14px;padding:0 7% 0 20px}}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item{background:linear-gradient(268.87deg, #FFC83E -5.09%, #FF7347 51.96%);animation:opacity 1s;box-shadow:inset 0 0 20px #fff}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(1){color:#fff;padding-bottom:0}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(1)::after{transform:rotate(270deg)}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(2){max-height:100vh;zoom:1;padding:25px 7% 25px 35px}@media(max-width: 425px){#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(2){font-size:14px;padding:25px 7% 25px 20px}}@keyframes opacity{0%{filter:grayscale(1)}100%{filter:grayscale(0)}}